@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&family=Roboto:wght@700;900&display=swap');

body {
  background-color: #003461;
}

.taskItem {
  padding: 3px 0px 3px 0px;
  font-family: 'Indie Flower';
}

.taskContainer {
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.formContainer {
  display: flex;
  align-items: center;
  column-gap: 10px;
  margin-top: 10px;
}

.taskInput {
  border: 2px solid #00c6bc;
  border-radius: 5px;
  padding: 10px 5px 10px 5px;
}

.pageContainer {
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  background-color: whitesmoke;
  width: 30%;
  border-radius: 5px;
}

.inputLabelContainer {
}

.taskLabel {
  font-family: 'Roboto', sans-serif;
  padding-right: 5px;
  margin-top: auto;
  margin-bottom: auto;
}

.button {
  margin-left: auto;
  margin-right: auto;
  width: 40%;
  background-color: #00c6bc;
  font-size: 15px;
  padding: 10px;
  color: white;
  font-weight: bold;
  border: 2px solid #00c6bc;
  border-radius: 5px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.button:hover {
  background-color: #003461;
}

#root {
  display: block;
  height: 100vh;
  border-radius: 5px;
}
